export default {
  props: ['text'],
  methods: {
    copyText() {
      const self = this
      navigator.clipboard.writeText(self.text).then(function() {
        alert(self.text + ' copied to clipboard')
      }, function() {
        alert('Copying text does not work in this browser')
      })
    }
  }
}
